<template>
  <div class="wrapper">
    <h1 class="title">商品資訊</h1>
    <OrderBox class="cart-info">
      <span slot="before">商品明細</span>
      <span slot="before">訂單編號 {{ order.id }}</span>

      <tr slot="table" v-for="(el, i) in cart" :key="i">
        <th>{{ el.name }} &times; {{ el.quantity }}</th>
        <td>NT$ {{ el.perPrice * el.quantity }}</td>
      </tr>

      <span slot="after">總價</span>
      <span slot="after">NT$ {{ this.$store.getters.calc_total }}</span>
    </OrderBox>

    <h1 class="title">付款資訊</h1>
    <Box class="payment-info">
      <tr slot="table">
        <th>付款方式</th>
        <td>{{ payment_format.payment_info.name }}</td>
      </tr>
      <tr slot="table" v-for="(el, i) in confirms" :key="i">
        <th>{{ form[el].label }}</th>
        <td>{{ getData(el) }}</td>
      </tr>
    </Box>

    <span slot="after" class="alert" v-if="!readyToUpload">
      <i class="fas fa-exclamation-triangle"></i>
      警告：付款資訊不完整
      <p>您仍然可以嘗試送出訂單，但為免權益受損，建議回前頁填妥表單資訊。</p>
    </span>
    <footer>
      <RouteButton location="Step2">上一步</RouteButton>
      <RouteButton @click.native="send" location="Step4">送出訂單</RouteButton>
    </footer>
  </div>
</template>

<script>
export default {
  computed: {
    readyToUpload() {
      return this.$store.state.readyToUpload;
    },
    order() {
      return this.$store.state.order;
    },
    cart() {
      return this.order.cart;
    },
    payment_format() {
      return this.$store.getters.payment_format;
    },
    form() {
      return this.payment_format.form;
    },
    confirms() {
      return this.payment_format.confirm_info;
    },
    formData() {
      return this.$store.state.cachedFormData;
    }
  },
  methods: {
    send() {
      this.$store.dispatch("send");
    },
    getData(key) {
      if (!this.formData) {
        return;
      }
      return this.formData.get(key);
    }
  }
};
</script>

