  margin: var(--gap-xl) 0 var(--gap-big) 0;
  padding: 3px calc(var(--gap-xl) - 5px);
  text-align: left;
  font-weight: bold;
  border-left: 5px solid var(--color-active);
}
.alert {
  display: block;
  position: relative;
  padding: var(--gap-xl) 0 0 0;
  p {
    font-size: var(--font-size-small);
    text-align: center;
    text-indent: 2em;
    line-height: 1.5em;
    padding: var(--gap-small);
  }
}
</style>
