<template>
  <div class="wrapper">
    <img src="@/assets/undraw_Credit_card_3ed6.svg" alt />
    <h1 class="title">
      <span>付費方案：{{ payment_format.payment_info.name }}</span>
      <span class="alert" v-if="!readyToUpload">
        <i class="fas fa-exclamation-circle"></i>
        請完成必填項目
      </span>
    </h1>
    <form id="payment-form">
      <div class="input-card" v-for="(el, i) in inputs" :key="i">
        <label>
          {{ el[1].label }}
          <i
            v-if="
              payment_format.required_field.includes(el[0]) &&
                !getCachedData(el[0])
            "
            class="fas fa-exclamation-circle"
          ></i>
        </label>
        <input
          :name="el[0]"
          :type="el[1].type"
          :placeholder="el[1].placeholder"
          :value="getCachedData(el[0])"
          @input="updateCache"
        />
      </div>
    </form>
    <footer>
      <RouteButton location="Step1">上一步</RouteButton>
      <RouteButton location="Step3">下一步</RouteButton>
    </footer>
  </div>
</template>

<script>
export default {
  mounted() {
    const form = document.querySelector("#payment-form");
    this.form = form;
    this.updateCache();
  },
  data() {
    return {
      form: undefined
    };
  },
  computed: {
    readyToUpload() {
      return this.$store.state.readyToUpload;
    },
    payment_format() {
      return this.$store.getters.payment_format;
    },
    inputs() {
      const pairs = [];
      for (const pair of Object.entries(this.payment_format.form)) {
        pairs.push(pair);
      }
      return pairs;
    },
    formData() {
      return this.$store.state.formData;
    },
    cachedFormData() {
      return this.$store.state.cachedFormData;
    }
  },
  methods: {
    updateCache() {
      const formData = new FormData(this.form);
      this.$store.dispatch("updateCachedFormData", formData);
      this.$store.commit("SET_READY_TO_UPLOAD", this.verify(formData));
    },
    send() {
      this.$store.dispatch("send", new FormData(this.form));
    },
    verify(formData) {
      for (const pair of formData.entries()) {
        if (this.payment_format.required_field.includes(pair[0]) && !pair[1]) {
          return false;
        }
      }
      return true;
    },
    getCachedData(key) {
      if (this.cachedFormData) {
        // alert("請輸入付款資訊");
        return this.cachedFormData.get(key);
      }
      return undefined;
    }
  }
};
</script>

